.main {
  margin: 0;
  padding-top: 20%;
  height: 100%;
  position: relative;
}

.main-label {
  color: white;
  font-size: 1em;
  margin: 0 0 0 5px;
}

.main-searching {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
}

.main-searching__color {
  color: black;
  font-weight: bold;
  text-align: center;
}

@media screen and (min-width:992px) {
  .main-search {
    width: 800px;
    margin: 0 auto;
    padding: 1px;
    display: flex;
    flex-direction: column;
  }

  .main-input {
    width: 798px;
    height: 60px;
    font-size: 32px;
    box-sizing: border-box;
  }

  .main-select {
    width: 300px;
    margin: 0;
    border-radius: 0;
    box-sizing: border-box;
  }

  .main-select__direction {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 10px 0 10px 0;
    padding: 0;
  }
}

@media screen and (min-width:564px) and (max-width:992px) {
  .main-search {
    width: 500px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
  }

  .main-input {
    width: 500px;
    height: 60px;
    font-size: 32px;
    box-sizing: border-box;
  }

  .main-select {
    width: 500px;
    margin: 10px 0 0 0;
    border-radius: 0;
    box-sizing: border-box;
  }

  .main-select__direction {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;
  }

}

@media screen and (max-width:564px) {
  .main-search {
    width: 300px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
  }

  .main-input {
    width: 300px;
    height: 60px;
    font-size: 32px;
    box-sizing: border-box;
  }

  .main-select {
    width: 100%;
    margin: 10px 0 0 0;
    border-radius: 0;
    box-sizing: border-box;
  }

  .main-select__direction {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
  }

}

.main-button {
  width: 55px;
  height: 55px;
  background-color: white;
  border: 0;
  outline: 0;
  position: absolute;
  top: 4px;
  right: 0;
}

.main-text {
  position: relative;
}

.footer {
  height: 30px;
  width: auto;
  padding: 2px;
  margin: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: flex-end;
  position: fixed;
  bottom: 0;
  width: 100%;
}

.footer-button {
  height: 20px;
  width: 100px;
  margin: 5px;
  background-color: white;
  color: gray;
  border: 0;
  outline: 0;
}

.footer-button:hover {
  color: black;
}

.footer-button:active {
  color: white;
  background-color: black;
  color: gray;
  border: 1px solid white;
  outline: 0;

}