.book {
    margin: 2px auto;
    padding: 5px 2px 2px 2px;
    width: 280px;
    height: 440px;
    position: relative;
}

.book:after {
    content: "";
    width: 280px;
    height: 440px;
    position: absolute;
    top: 0;
    left: 0;
    background-color: white;
    opacity: 0.7;
    z-index: -1;
}

.book-img {
    margin: 0 0 0 60px;
}

.book-p {
    margin: 0 0 0 30px;
    font-size: 12px;
    color: black;
}

.OneBook-p {
    margin: 0 0 0 30px;
    font-size: 16px;
    color: black;
}

.book-h4 {
    margin: 0 0 0 30px;
    color: black;
}

.main-resault {
    padding: 5px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
}

.main-gotBook {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 5;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    z-index: 10;
}

@media screen and (min-width:992px) {
    .main-gotBook {
        position: fixed;
        width: 100%;
        height: 100%;
        padding: 5px 0 0 0;
        top: 0;
        left: 0;
        z-index: 5;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        z-index: 10;
    }
    .bigBook-img {
        width: 41vw;
        height: 41vw;
        padding: 5px;
        margin: 0;
        background-color: white;
    }

    .bigBook-text {
        width: 41vw;
        height: 41vw;
        margin: 0;
        padding: 5px;
        background-color: white;
        position: relative;
    }
    .bigBook-button {
        position: absolute;
        top: 5px;
        right: 5px;
        width: 20px;
        height: 20px;
        padding: 0 0 3px 0;
        background-color: white;
        color: gray;
        border: solid 2px black;
    }
    .bigBook-button:active {
        color: white;
        background-color: black;
    }
    .OneBook-p {
        margin: 0 0 0 30px;
        font-size: 16px;
        color: black;
    }
}
